.footer {
  padding-top: 40px;
  padding-bottom: 52px;
  background-color: #e8e8e8;

  @include mobile {
    padding-top: 30px;
    padding-bottom: 20px;
  }

  &__container {
    position: relative;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    // grid-template-rows: auto;
    gap: 30px;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    // grid-template-areas:
    //   "1"
    //   "."
    //   "2"
    //   "."
    //   "3"
    //   "."
    //   "4"
    //   "."
    //   "5";

    @include mobile {
      gap: 0;
    }
  }

  &__left {
    grid-column: 1 / span 5;
    display: flex;
    flex-direction: column;
    padding-top: 13px;
    padding-left: 70px;

    @include mobile {
      text-align: center;
      align-items: center;
      padding-left: 0;
    }
  }

  &__left-policy {
    display: none;

    @include mobile {
      display: block;
      margin-bottom: 21px;
    }
  }

  &__logo {
    width: 296px;
    height: 107px;
    margin-bottom: 34px;

    @include mobile {
      width: 200px;
      height: 72px;
      margin-bottom: 17px;
    }

    & svg {
      width: inherit;
      height: inherit;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 48px;
    color: var(--dark);
    font-size: 16px;
    font-weight: 500;
    line-height: normal;

    @include mobile {
      margin-bottom: 18px;
    }
  }

  &__stamp {
    color: var(--dark);
    font-size: 16px;
    font-weight: 600;
    line-height: normal;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__center {
    grid-column: 6 / span 3;

    @include mobile {
      grid-column: auto;
    }
  }

  &__nav {
    @include mobile {
      display: none;
    }
  }

  &__policy {
    display: block;
    margin-bottom: 49px;

    @include mobile {
      display: none;
    }
  }

  &__copyright {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    @include mobile {
      justify-content: center;
    }

    &__text {
      color: var(--dark);
      font-size: 16px;
      font-weight: 600;
      line-height: normal;
    }
  }

  &__right {
    padding-top: 10px;
    grid-column: 9 / span 4;

    @include mobile {
      display: none;
    }
  }
}

.nav {
  &__list {
    &_footer {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 0;
      margin-bottom: 20px;
      font-size: 16px;
      font-weight: 600;
      line-height: 257%; /* 41.6px */
    }
  }
}

.copyright {
  &__text {
    display: block;
    margin-right: 11px;
  }
}

#footer-tel {
  margin-bottom: 3px;
}

.scroll-up-link {
  position: absolute;
  top: -140px;
  right: -29px;
  width: 152px;
  height: 152px;
  display: none;
  opacity: 0;

  & svg circle {
    transition: fill 0.5s ease-in-out;
  }
}

.scroll-up-link-showed {
  display: block;
  animation: 0.3s linear btn-showing forwards;

  &:focus-visible {
    outline: none;

    & svg circle {
      fill: #245db3;
    }
  }

  @include mobile {
    display: none;
  }
}

@keyframes btn-showing {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
