.hero {
  position: relative;

  @include mobile {
    padding-bottom: 29px;
  }

  &__button {
    position: absolute;
    left: 23%;
    bottom: 30%;
    padding: 20px 44px;
    color: var(--white-color);
    font-weight: 700;
    line-height: normal;
    z-index: 100;
    border-radius: 15px;
    background: linear-gradient(88deg, #0041a4 3.4%, #00a386 98.93%);
    box-shadow: 0px 10px 50px 0px rgba(0, 221, 185, 0.25);

    @include mobile {
      left: 19px;
      bottom: 96px;
      padding: 21px 50px;
    }
  }

  &__pagination {
    bottom: 20%;
    left: 23%;
    z-index: 22;

    @include mobile {
      bottom: 27px;
      left: 25px;
    }
  }

  &__foot {
    position: absolute;
    bottom: -4px;
    z-index: 100;
  }
}

.pag {
  position: absolute;
  display: flex;
  align-items: center;
  width: 270px;
  height: 22px;

  &__number {
    display: block;
    color: var(--dark);
    font-size: 18px;
    font-weight: 700;
    line-height: normal;
  }
}
.swiper {
  width: 100%;
  height: auto;
}

.swiper-slide {
  width: 100%;
  height: auto;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;

  &__container {
    padding-top: 150px;
    padding-bottom: 76px;
    gap: 80px;
    display: grid;
    grid-template-columns: 1fr 1fr;

    @include mobile {
      grid-template-rows: 1fr auto;
      padding-top: 88px;
      padding-bottom: 21px;
      gap: 14px;
      grid-template-columns: 1fr;
      background-image: image-set(
        "../img/hero/hero-bg-mobile.jpg" type("image/jpg"),
        "../img/hero/hero-bg-mobile.webp" type("image/webp")
      );
      // background-image: url(../../img/hero/hero-bg-mobile.jpg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }
}

.swiper-slide_1 {
  background-image: image-set(
    "../img/hero/hero-slide-bg-1.jpg" type("image/jpg"),
    "../img/hero/hero-slide-bg-1.webp" type("image/webp")
  );

  @include mobile {
    background-image: none;
  }
}
.swiper-slide_2 {
  background-image: image-set(
    "../img/hero/hero-slide-bg-2.jpg" type("image/jpg"),
    "../img/hero/hero-slide-bg-2.webp" type("image/webp")
  );
  @include mobile {
    background-image: none;
  }
}
.swiper-slide_3 {
  background-image: image-set(
    "../img/hero/hero-slide-bg-3.jpg" type("image/jpg"),
    "../img/hero/hero-slide-bg-3.webp" type("image/webp")
  );
  @include mobile {
    background-image: none;
  }
}

.slide {
  padding-top: 153px;

  @include mobile {
    padding-top: 87px;
  }

  &__left {
    position: relative;
    max-width: 515px;
    margin-right: 40px;
    padding-left: 86px;
  }

  &__title {
    margin: 0;
    padding-left: 85px;
    max-width: 400px;
    padding-top: 37px;
    color: var(--east-bay);
    font-size: 50px;
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;

    @include mobile {
      padding-bottom: 158px;
      grid-row-start: 2;
      padding-top: 0;
      text-align: center;
      padding-left: 0px;
      font-size: 20px;
    }
  }

  &__right {
    width: 100%;
    max-width: 505px;

    @include mobile {
      padding-left: 36px;
      max-width: 234px;
    }
  }
}

.swiper-pagination {
  position: relative;
}

.swiper-pagination-bullet {
  width: 51px;
  height: 4px;
  border-radius: 100px;
  background-color: var(--cabalt);
  transition: opacity 0.3s ease-in-out;
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  top: 0;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets
  .swiper-pagination-bullet:not(:last-child) {
  margin-right: 20px;
}

#btn-contract-modal {
  position: absolute;
  opacity: 0;
}

input:focus + .form__contract-descr::before {
  outline: 1px solid #54994b;
  outline-offset: 2px;
}

input:checked + .form__contract-descr {
  outline: none;
}

.form__contract-descr.form__contract-descr_modal {
  &::after {
    left: 9px;
    bottom: 9px;
  }
}

.form__btn.form__btn_modal {
  background: linear-gradient(88deg, #0041a4 3.4%, #00a386 98.93%);
  box-shadow: 0px 10px 50px 0px rgba(0, 221, 185, 0.25);
}
