.header {
  position: absolute;
  width: 100%;
  top: 35px;
  z-index: 22;

  @include mobile {
    top: 0;
  }

  &__container {
    overflow: hidden;

    @include mobile {
      padding: 0;
      max-width: 100%;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 22px 70px;
    background-color: var(--white-color);
    background-color: var(--white-color);
    border-radius: 15px;

    @include mobile {
      position: relative;
      padding: 15px 20px;
      border-top-right-radius: 0;
      border-top-left-radius: 0;
      z-index: 1000;
    }
  }

  &__logo {
    width: 100px;
    height: 37px;

    @include mobile {
      width: 80px;
      height: 29px;
    }
  }

  &__tel {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    padding-left: 40px;
    background-image: url(../img/tel-icon.svg);
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 30px 30px;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    color: var(--dark);
    transition: color 0.3s ease-in-out;

    &:focus-visible {
      outline: none;
      color: var(--dark-lime-green);
    }
    &:focus {
      outline: none;
      color: var(--dark-lime-green);
    }

    &:hover {
      color: var(--dark-lime-green);
    }

    &:active {
      color: #295523;
    }

    @include mobile {
      display: none;
    }
  }
}

.nav {
  align-items: center;
  margin-left: auto;
  margin-right: 38px;

  @include mobile {
    display: none;
  }

  &__list {
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;

    @include tablet {
      // justify-content: flex-start;
      // gap: 33px;
    }

    @include small-tablet {
      // justify-content: space-between;
      // margin-right: auto;
      // gap: 10%;
    }

    @include mobile {
      // flex-direction: column;
      // align-items: flex-start;
      // gap: 30px;
    }

    &_burger {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 0;
    }
  }

  &__link {
    position: relative;
    font-weight: 600;
    font-size: 18px;
    text-align: center;
    color: var(--dark);
    transition: color 0.3s ease-in-out;

    @include mobile {
      font-size: 16px;
      line-height: 40px;
    }

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 2px;
      background-color: var(--cabalt);
      bottom: 0;
      left: 0;
      transform-origin: right;
      transform: scaleX(0);
      transition: transform 0.3s ease-in-out;
    }

    &:focus {
      outline: none;
      color: var(--cabalt);

      &::before {
        opacity: 1;
        visibility: visible;
      }
    }

    &:active {
      color: var(--strong-orange);
    }

    @include hover {
      color: var(--cabalt);

      &::before {
        transform-origin: left;
        transform: scaleX(1);
      }
    }
  }

  &__burger-tel {
    position: fixed;
    display: none;
    padding-left: 22px;
    font-family: var(--second-family);
    font-size: 16px;
    color: var(--vivid-orange);
    background-image: url("../img/tel.svg");
    background-position: left center;
    background-size: 22px 22px;
    background-repeat: no-repeat;
    transition: color 0.3s ease-in-out, background-size 0.3s ease-in-out;

    &:focus {
      outline: none;
      color: var(--strong-orange-ui);
    }

    &:hover {
      color: var(--strong-orange);
      background-size: 26px 26px;
    }

    // @include tablet {
