}

.flex {
  display: flex;
}

.flex-ai-center {
  align-items: center;
}

.flex-jc-between {
  justify-content: space-between;
}

.nav.menu--active {
  visibility: visible;
  transform: none;
}

.burger-navigation {
  background-color: var(--white-color);
  width: 100%;
  position: fixed;
  top: 47px;
  border-radius: 0px 0px 20px 20px;
  transform: translateY(-123%);
  transition: transform 0.2s;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;

  &__nav {
    display: none;
    margin-right: 0;
    margin-bottom: 13px;

    @include mobile {
      display: block;
    }
  }
}

.links.burger-navigation__links {
  display: none;

  @include mobile {
    display: flex;
    justify-content: center;
    margin-bottom: 0;
    gap: 27px;
  }
}

.showBurger {
  transform: translateY(0);
}
