.about {
  padding-top: 100px;
  padding-bottom: 100px;

  @include small-tablet {
    padding-top: 28px;
    padding-bottom: 50px;
  }

  @include mobile {
    padding-top: 0;
  }

  &__container {
    @include small-tablet {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__top {
    position: relative;
    padding: 30px 70px;
    padding-top: 27px;
    padding-bottom: 94px;
    height: auto;
    margin-bottom: 100px;
    background-image: image-set(
      "../img/about/about-bg.jpg" type("image/jpg"),
      "../img/about/about-bg.webp" type("image/webp")
    );
    background-position: left top;
    background-repeat: no-repeat;
    background-size: cover;
    transition: height 0.3s ease-in-out;

    @include mobile {
      border-radius: 20px;
      height: 100%;
      max-height: 480px;
      padding-left: 20px;
      padding-right: 20px;
      margin-bottom: 50px;
      padding-top: 20px;
      padding-bottom: 0;
      background-image: image-set(
        "../img/about/about-bg-mobile.jpg" type("image/jpg"),
        "../img/about/about-bg-mobile.webp" type("image/webp")
      );
      &_open {
        max-height: 100%;
      }
    }
  }

  &__content {
    transition: transform 0.3s ease-in-out;

    @include small-tablet {
      gap: 22px;
      flex-direction: column-reverse;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    color: var(--east-bay);
    line-height: 85%;
    text-transform: uppercase;

    @include mobile {
      text-align: center;
      margin-bottom: 12px;
    }
  }

  &__rule {
    // margin-bottom: 19px;
    // color: var(--dark-lime-green);
    // font-size: 25px;
    // font-weight: 700;
    // line-height: normal;
  }

  &__desrc {
    // color: var(--dark);
    // font-size: 16px;
    // font-weight: 500;
    // line-height: normal;
  }

  &__button {
    bottom: -35px;
    left: 40%;

    @include small-tablet {
      display: block;
    }
  }
}

.open-button {
  display: none;
  position: absolute;

  @include small-tablet {
    display: block;
  }
}

.content {
  display: flex;
  justify-content: space-between;

  &__left {
    width: calc(50% - 15px);

    @include small-tablet {
      width: 100%;
      padding-bottom: 16px;
      // max-height: 480px;
      // white-space: nowrap; /* Запрещаем перенос строк */
      // overflow: hidden; /* Обрезаем все, что не помещается в область */
      // background: #fc0; /* Цвет фона */
      // padding: 5px; /* Поля вокруг текста */
      // text-overflow: ellipsis; /* Добавляем многоточие */
    }
  }

  &__rule {
    margin: 0;
    margin-top: -4px;
    margin-bottom: 19px;
    color: var(--dark-lime-green);
    font-size: 25px;
    font-weight: 700;
    line-height: normal;

    @include small-tablet {
      font-size: 18px;
      margin-bottom: 9px;
    }
  }

  &__descr {
    color: var(--dark);
    font-weight: 500;
    line-height: normal;
    transition: height 0.4s ease-in-out;

    @include small-tablet {
      height: 119px;
      overflow: hidden;
    }

    & > span {
      display: block;
      margin-bottom: 20px;
    }

    &_open {
      height: auto;
    }
  }

  &__descr:not(:last-child) {
    margin-bottom: 19px;
  }

  &__right {
    width: calc(50% - 15px);

    @include small-tablet {
      width: 100%;
      height: 190px;
    }
  }

  &__image {
    border-radius: 20px;

    @include small-tablet {
      height: inherit;
    }
  }
}

.standard {
  &__list {
    gap: var(--offset);

    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 23px;
    width: 100%;
    max-width: 270px;
    font-size: 16px;
    text-align: center;
    font-weight: 700;
    line-height: normal;
    color: var(--dark);

    &:nth-child(3) {
      transform: translateY(8px);
    }

    @include small-tablet {
      gap: 25px;
    }
  }
}
