.application {
  padding-bottom: 100px;

  @include mobile {
    padding-top: 20px;
    background-image: image-set(
      "../img/application/application-bg-mobile.jpg" type("image/jpg"),
      "../img/application/application-bg-mobile.webp" type("image/webp")
    );
    // background-image: url(../../img/application/application-bg-mobile.jpg);
    background-repeat: no-repeat;
  }

  &__wrapper {
    padding: 27px 70px;
    padding-bottom: 100px;
    background-image: image-set(
      "../img/application/application-bg.jpg" type("image/jpg"),
      "../img/application/application-bg.webp" type("image/webp")
    );
    // background-image: url(../../img/application/application-bg.jpg);
    background-position: left top;
    background-repeat: no-repeat;
    background-size: contain;

    @include mobile {
      padding-left: 0;
      padding-right: 0;
      padding: 0;
      background-image: none;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    max-width: 350px;
    color: #79a174;
    font-weight: 700;
    line-height: 100%; /* 45px */
    text-transform: uppercase;

    @include mobile {
      margin-bottom: 30px;
      text-align: center;
    }
  }
}

.form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 70px 60px 200px 32px 25px 36px 60px;
  grid-column-gap: 30px;
  grid-template-areas:
    "name tel"
    ". ."
    "text text"
    ". ."
    "contract contract"
    ". ."
    "btn btn";

  @include mobile {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-template-areas:
      "name"
      "tel"
      "text"
      "contract"
      "btn";
  }

  &__contract {
    position: relative;
    grid-area: contract;
    color: var(--dark);
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    // color: red;
    text-align: center;
    // display: flex;
    // justify-content: center;
    align-items: center;
    text-align: center;
    display: inline-block;
    justify-self: center;
  }

  &__input-box {
    position: relative;
    margin-bottom: 36px;
    border-radius: 15px;
    color: #878787;
    font-size: 18px;
    font-weight: 500;
    line-height: normal;
    transition: outline 0.3s ease-in-out;

    &_name {
      grid-area: name;
      height: 70px;
    }

    &_tel {
      grid-area: tel;
      height: 70px;
    }

    &_textarea {
      grid-area: text;
      width: 100%;
      height: 200px;
      // width: 100%;
    }
  }

  &__input-inp {
    padding: 25px 30px;
    width: 100%;
    border-radius: 15px;
    border: 1px solid white;
    transition: border 0.3s ease-in-out, transform 0.3s ease-in-out;

    &::placeholder {
      color: #878787;
      font-size: 18px;
      font-weight: 500;
      line-height: normal;
    }

    &.focus-visible {
      outline: none;
    }

    &_textarea {
      padding-top: 20px;
      height: 100%;
    }

    &:focus {
      transform: scale(1.1);
      border: 1px solid #54994b;
    }
  }

  // &__contract {
  //   grid-area: contract;
  //   color: var(--dark);
  //   font-size: 16px;
  //   font-weight: 500;
  //   line-height: normal;
  // }

  &__contract-descr {
    display: flex;
    align-items: center;
    margin-left: 50px;
    // color: var(--dark);
    // font-size: 16px;
    // font-weight: 500;
    // line-height: normal;
    // margin-left: 16px;
    // height: 100%;

    &::before {
      content: "";
      position: absolute;
      left: 0px;
      top: -9px;
      display: inline-block;
      width: 40px;
      height: 40px;
      border-radius: 10px;
      background-color: white;

      @include mobile {
        top: 6px;
      }
    }

    &::after {
      content: url(../img/contract-arrow.svg);
      position: absolute;
      left: 9px;
      bottom: -21px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }

  &__btn {
    grid-area: btn;
    justify-self: center;
    padding: 22px 94px;
    border-radius: 15px;
    background: #73a56c;
    color: var(--white-color);
    font-size: 16px;
    font-weight: 700;
    line-height: normal;
    transition: outline 0.5s ease-in-out;

    @include mobile {
      width: 100%;
      height: 60px;
    }

    &:focus-visible {
      outline: 2px solid rgb(33, 33, 204);
      border: none;
    }

    &:hover {
      outline: 2px solid rgb(33, 33, 204);
      border: none;
    }
  }
}

.just-validate-error-label {
  position: absolute;
  top: 0;
  left: 25px;
}

#btn-contract {
  position: absolute;
  opacity: 0;
