.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 3px solid var(--vivid-orange);
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: var(--white-color);
  transition: border 0.3s ease-in-out, background-color 0.3s ease-in-out;

  @include mobile {
    font-size: 14px;
  }
}

.btn_fill {
  background-color: var(--vivid-orange);

  &:focus {
    outline: none;
    border-color: var(--strong-orange-ui);
    background-color: var(--strong-orange-ui);
  }

  &:hover {
    border: 3px solid var(--strong-orange);
  }

  &:active {
    border-color: transparent;
    background-color: var(--strong-orange-ui);
  }
}
