// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Gilroy", sans-serif;
  --content-width: 1170px;
  --tablet-width: 962px;
  --small-tablet-width: 714px;
  --mobile-width: 278px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --tablet-container-width: calc(
    var(--tablet-width) + (var(--container-offset) * 2)
  );
  --small-tablet-container-width: calc(
    var(--small-tablet-width) + (var(--container-offset) * 2)
  );
  --mobile-container-width: calc(
    var(--mobile-width) + (var(--container-offset) * 2)
  );
  --offset: 30px;

  // sizes
  --title-size: 45px;
  --main-size: 16px;
  --second-size: 24px;

  // colors
  --white-color: #fff;
  --dark: #272727;
  --east-bay: #465067;
  --dark-lime-green: #54994b;
  --cabalt: #0041a4;
  // --very-dark-gray: #202020;
  // --vivid-orange: #ff6e30;
  // --strong-orange: #ce4307;
  // --strong-orange-ui: #bc572b;
  // --light-gray: #cacaca;
  // --dark-gray: #999;
  // --very-light-gray: #e9e9e9;
}
