.contacts-section {
  &__container {
    display: flex;
    justify-content: space-between;
    padding-left: 86px;
    padding-right: 86px;
    padding-bottom: 100px;

    @include mobile {
      flex-direction: column;
      gap: 31px;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 50px;
    }
  }

  &__left {
    @include mobile {
      text-align: center;
    }
  }

  &__right {
    border-radius: 15px;
    overflow: hidden;

    @include mobile {
      width: 100%;
      height: 280px;
    }
  }
}
.contacts {
  &__title {
    margin: 0;
    margin-bottom: 23px;
    color: var(--east-bay);
    font-weight: 700;
    line-height: 100%; /* 45px */
    text-transform: uppercase;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 17px;
    margin-bottom: 20px;

    @include mobile {
      align-items: center;
    }
  }

  &__item {
    background-repeat: no-repeat;
    background-size: 30px 30px;
    background-position: left top;

    &_adress {
      background-image: url(../img/contacts/contacts-adress.svg);
    }

    &_phone {
      background-image: url(../img/contacts/contacts-phone.svg);
    }

    &_time {
      background-image: url(../img/contacts/contacts-time.svg);
    }
  }

  &__right {
    overflow: hidden;
    border-radius: 15px;
  }
}

.links {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 26px;

  @include mobile {
    margin-bottom: 30px;
  }

  &__item {
    display: block;
    width: 100%;
    height: 100%;

    & svg {
      transition: fill 0.3s ease-in-out;
    }

    &:focus {
      outline: none;
    }
    &:focus-visible {
      outline: none;
    }

    &:focus svg {
      outline: none;
      fill: var(--dark-lime-green);
    }
    &:focus-visible svg {
      outline: none;
      fill: var(--dark-lime-green);
    }

    &:hover svg {
      fill: var(--dark-lime-green);
    }
  }
}

.list-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  padding-left: 41px;
  color: var(--dark);

  @include mobile {
    align-items: center;
    padding-left: 0;
    background-position: top center;
    align-items: center;
    padding-top: 35px;
  }

  &__top {
    margin-bottom: 6px;
    font-size: 18px;
    font-weight: 700;
    line-height: normal;
    color: var(--dark);

    &_phone {
      transition: color 0.3s ease-in-out;

      &:focus-visible {
        outline: none;
        color: var(--dark-lime-green);
      }
      &:focus {
        outline: none;
        color: var(--dark-lime-green);
      }

      &:hover {
        color: var(--dark-lime-green);
      }

      &:active {
        color: #295523;
      }
    }
  }

  &__bottom {
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    text-decoration: underline;
    transition: color 0.3s ease-in-out;

    &:focus-visible {
      outline: none;
      color: var(--dark-lime-green);
    }
    &:focus {
      outline: none;
      color: var(--dark-lime-green);
    }

    &:hover {
      color: var(--dark-lime-green);
    }

    &:active {
      color: #295523;
    }
  }

  // &__right {
  //   border-radius: 15px;
  //   overflow: hidden;
  // }
}

.ymaps-2-1-79-controls__control {
  display: none !important;
}

.ground-pane {
  filter: grayscale(0) !important;
}

.policy {
  color: #8c8c8c;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
  text-decoration-line: underline;
  transition: color 0.3s ease-in-out;

  &:focus-visible {
    outline: none;
    color: var(--dark-lime-green);
  }
  &:focus {
    outline: none;
    color: var(--dark-lime-green);
  }

  &:hover {
    color: var(--dark-lime-green);
  }

  &:active {
    color: #295523;
  }
}

.contacts-section__right.map {
  @include mobile {
    width: 100% !important;
    height: 280px !important;
  }
}
