.service {
  position: relative;
  padding-top: 25px;
  padding-bottom: 163px;
  background-image: image-set(
    "../img/service/service-bg.jpg" type("image/jpg"),
    "../img/service/service-bg.webp" type("image/webp")
  );
  // background-image: url(../../img/service/service-bg.jpg);
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;

  @include mobile {
    padding-bottom: 60px;
    position: relative;
    background-image: image-set(
      "../img/service/service-bg-mobile.jpg" type("image/jpg"),
      "../img/service/service-bg-mobile.webp" type("image/webp")
    );
    // background-image: url(../../img/service/service-bg-mobile.jpg);
  }

  &__pag {
    display: none;
    left: 26px;
    bottom: 25px;

    @media (max-width: 1200px) {
      display: flex;
      left: 38%;
      bottom: 11%;
    }

    @include mobile {
      left: 7%;
      bottom: 3%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 54px;
    text-align: center;
    color: var(--east-bay);
    font-weight: 700;
    line-height: 85%; /* 38.25px */
    text-transform: uppercase;

    @include mobile {
      margin-bottom: 26px;
    }
  }

  &__list {
    justify-content: space-between;
    flex-wrap: wrap;
    gap: var(--offset);
  }

  &__icon {
    margin-bottom: 45px;
    z-index: 40;
  }

  &__link {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 40;

    // &:focus + .service__consultation {
    //   opacity: 1;
    //   -webkit-transform: translateY(0);
    //   -ms-transform: translateY(0);
    //   transform: translateY(0);
    // }
  }

  &__text {
    display: block;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 25px;
    height: 86px;
    z-index: 1;
    transition: z-index 0.3s ease-in-out;

    @media (max-width: 740px) {
      font-size: 14px;
    }
  }

  &__arrow {
    z-index: 40;
    transition: transform 0.4s ease-in-out;
  }

  &__consultation {
    padding-left: 20px;
    height: 211px;
    box-sizing: border-box;
    width: inherit;
    position: absolute;
    // padding-left: 10px;
    padding-top: 60px;
    padding-right: 10px;
    bottom: 0;
    // width: 100%;
    // height: 42%;
    border-radius: 100px 100px 20px 20px;
    background: #06f;
    // z-index: 1000;
    cursor: pointer;
    opacity: 0;
    transform: translateY(102%);
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  }

  &__consultation-text {
    color: var(--white-color);
    font-size: 25px;
    font-weight: 700;
    line-height: 100%; /* 25px */
  }

  &__swiper {
    width: inherit;
    height: 500px;
  }

  &__swiper-slide {
    // mix-blend-mode: multiply;
    // padding: 15px;
    padding-bottom: 46px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
    // gap: 45px;
    // width: 270px;
    // height: 500px;
    border-radius: 20px;
    background: linear-gradient(180deg, rgba(0, 102, 255, 0) 31%, #000 100%);
    color: var(--white-color);
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    // background: #73a56c;
    // mix-blend-mode: multiply;
    overflow: hidden;

    &::before {
      position: absolute;
      content: "";
      display: block;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      // width: 100%;
      // height: 100%;
      background: linear-gradient(
        180deg,
        rgba(0, 102, 255, 0.4) 31%,
        #000 100%
      );
      border-radius: 20px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &:hover::before {
      opacity: 1;
    }

    &:hover .service__consultation {
      opacity: 1;
      transform: translateY(0);
    }

    &_1 {
      // background: #73a56c;
      // mix-blend-mode: multiply;
      background-image: image-set(
        "../img/service/services-1.jpg" type("image/jpg"),
        "../img/service/services-1.webp" type("image/webp")
      );
      // background-image: url(../../img/service/services-1.jpg);
      // background-repeat: no-repeat;
      // background-size: cover;
      // background-position: center;
      // background: linear-gradient(180deg, rgba(0, 102, 255, 0) 31%, #000 100%);
    }
    &_2 {
      // background: #73a56c;
      // mix-blend-mode: multiply;
      background-image: image-set(
        "../img/service/services-2.jpg" type("image/jpg"),
        "../img/service/services-2.webp" type("image/webp")
      );
      // background-image: url(../../img/service/services-2.jpg);
      // background-repeat: no-repeat;
      // background-size: cover;
      // background-position: center;
      // background: linear-gradient(180deg, rgba(0, 102, 255, 0) 31%, #000 100%);
    }
    &_3 {
      // background: #73a56c;
      // mix-blend-mode: multiply;
      background-image: image-set(
        "../img/service/services-3.jpg" type("image/jpg"),
        "../img/service/services-3.webp" type("image/webp")
      );
      // background-image: url(../../img/service/services-3.jpg);
      // background-repeat: no-repeat;
      // background-size: cover;
      // background-position: center;
      // background: linear-gradient(180deg, rgba(0, 102, 255, 0) 31%, #000 100%);
    }
    &_4 {
      // background: #73a56c;
      // mix-blend-mode: multiply;
      background-image: image-set(
        "../img/service/services-4.jpg" type("image/jpg"),
        "../img/service/services-4.webp" type("image/webp")
      );
      // background-image: url(../../img/service/services-4.jpg);
      // background-repeat: no-repeat;
      // background-size: cover;
      // background-position: center;
      // background: linear-gradient(180deg, rgba(0, 102, 255, 0) 31%, #000 100%);
    }
  }
}

.service__swiper-slide:hover .service__arrow {
  transform: rotatex(180deg);
}

.service__swiper-slide:hover .service__text,
.service__link:focus-visible ~ .service__text {
  z-index: 0;
}

// .service__item:hover .service__arrow {
//   transform: rotatex(180deg);
// }

.service__link:focus-visible ~ .service__arrow {
  transform: rotatex(180deg);
}

// .service__item:focus .service__text {
//   transform: rotatex(180deg);
// }

.service__link:focus-visible ~ .service__consultation {
  opacity: 1;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0);
}

