//   z-index: 0;
// }

.item {
  mix-blend-mode: multiply;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  &__back-1 {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    // background: #73a56c;

    background: linear-gradient(180deg, rgba(0, 102, 255, 0) 31%, #000 100%);
    // z-index: -10;
  }
  &__back-2 {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #73a56c;
    mix-blend-mode: multiply;
    opacity: 0.6;
    /* overflow: hidden; */
    border-radius: 20px;
  }
}

.service-pagination {
  display: flex;
  justify-content: center;
}

.serv-pab-bullet {
  display: inline-block;
  width: 37px;
  height: 4px;
  border-radius: 100px;
  margin-right: 15px;
  background-color: var(--cabalt);
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
  opacity: var(--swiper-pagination-bullet-inactive-opacity, 0.2);
}

.serv-pag-bullet-active {
  opacity: 1;
}
