// МОДАЛЬНОЕ ОКНО

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 9998;
  background-color: rgba(0, 0, 0, 0.3);
  display: grid;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.4s, visibility 0.4s;

  &__mobile-logo {
    display: none;

    @include mobile {
      display: block;
    }
  }

  &__logo {
    @include mobile {
      display: none;
    }
  }

  &__box {
    position: relative;
    width: 1000px;
    height: 819px;
    z-index: 1;
    margin: 130px 15px;
    transform: scale(0);
    transition: transform 0.8s;
    border-radius: 20px;
    background: #eee;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
    border-radius: 20px;

    @include mobile {
      max-width: 320px;
      margin: 0;
      height: 100%;
      max-height: 885px;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 28px;
    padding-left: 50px;
    padding-right: 50px;
    background-color: var(--white-color);
    border-radius: 20px 20px 0px 0px;

    // @include mobile {
    //   display: none;
    // }
  }

  &__close-btn {
    border: none;
    background-color: transparent;
    padding: 0;
    top: 8px;
    right: 8px;
    cursor: pointer;

    & svg path {
      transition: stroke 0.3s ease-in-out, opacity 0.3s ease-in-out;
    }

    &:focus-visible {
      outline: none;
    }

    &:focus-visible svg path {
      opacity: 1;
      stroke-width: 3px;
      stroke: #465067;
    }

    &:hover svg path {
      opacity: 1;
      stroke-width: 3px;
      stroke: #465067;
    }

    &:active svg path {
      stroke: blue;
    }
  }

  &__descr {
    display: flex;
    justify-content: space-between;
    padding: 50px;

    @include mobile {
      flex-direction: column;
      text-align: center;
      padding: 20px;
    }
  }

  &__text {
    max-width: 335px;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    color: var(--east-bay);
    font-size: 65px;
    font-weight: 700;
    line-height: 100%; /* 65px */
    text-transform: uppercase;

    @include mobile {
      margin-bottom: 10px;
      font-size: 35px;
      line-height: 85%; /* 29.75px */
    }
  }

  &__subscribe {
    margin: 0;
    color: var(--dark);
    font-size: 30px;
    font-weight: 500;
    line-height: normal;

    @include mobile {
      font-size: 14px;
    }
  }

  &__form {
    padding-top: 4px;
  }
}

.modal.open {
  visibility: visible;
  opacity: 1;
}

.modal.open .modal__box {
  transform: scale(1);
}

.form {
  &_modal {
    display: flex;
    flex-direction: column;
    max-width: 500px;
  }

  &__input-box {
    margin-bottom: 30px;

    @include mobile {
      margin-bottom: 20px;
    }

    &_textarea {
      margin-bottom: 23px;

      @include mobile {
        margin-bottom: 20px;
      }
    }
  }

  &__contract-descr {
    font-size: 14px;
    font-weight: 500;
    line-height: normal;
    text-align: left;
  }

  &__contract {
    margin-bottom: 23px;

    @include mobile {
      margin-bottom: 20px;
    }
  }
}

.modal-back {
  background: rgba(232, 232, 232, 0.6);
  backdrop-filter: blur(12.5px);
}
