.portfolio {
  position: relative;
  padding-bottom: 100px;

  @include mobile {
    padding-bottom: 80px;
  }

  &__button {
    z-index: 100;
    left: 40%;
    bottom: 46px;
  }

  &__container {
    @include mobile {
      border-radius: 20px;
      height: 100%;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center top;
      background-image: image-set(
        "../img/portfolio/portfolio-bg-mobile.jpg" type("image/jpg"),
        "../img/portfolio/portfolio-bg-mobile.webp" type("image/webp")
      );
      // background-image: url(../../img/portfolio/portfolio-bg-mobile.jpg);
      background-size: cover;
    }

    &_open {
      max-height: 100%;
    }
  }

  &__pag {
    display: none;

    @include mobile {
      display: flex;
      justify-content: space-between;
      top: 250px;
      left: 0;
    }
  }

  &__wrapper {
    padding-top: 28px;
    padding-right: 70px;
    padding-left: 70px;
    width: 100%;
    background-image: image-set(
      "../img/portfolio/portfolio-bg.jpg" type("image/jpg"),
      "../img/portfolio/portfolio-bg.webp" type("image/webp")
    );
    // background-image: url(../../img/portfolio.portfolio-bg.jpg);
    background-position: top center;
    background-repeat: no-repeat;
    background-size: contain;

    @include mobile {
      padding-right: 0;
      padding-left: 0;
      background-image: none;
    }
  }

  &__descr {
    max-width: 397px;
  }

  &__title {
    margin: 0;
    margin-bottom: 41px;
    max-width: 300px;
    color: var(--east-bay);
    font-weight: 700;
    line-height: 100%; /* 45px */
    text-transform: uppercase;

    @include mobile {
      margin-bottom: 20px;
      text-align: center;
    }
  }

  &__place {
    margin: 0;
    margin-bottom: 10px;
    color: var(--dark-lime-green);
    font-size: 25px;
    font-weight: 500;
    line-height: normal;

    @include mobile {
      font-size: 18px;
      font-weight: 700;
    }
  }

  &__text {
    margin: 0;
    color: var(--dark);
    font-weight: 400;
    line-height: normal;

    @include mobile {
      overflow: hidden;
      max-height: 184px;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    & > span {
      display: block;
      margin-bottom: 20px;
      font-size: inherit;
      font-weight: inherit;
    }

    // &_open {
    //   overflow: visible;
    //   max-height: 100%;
    //   padding-bottom: 30px;
    // }
  }

  &__image {
    position: relative;
    overflow: hidden;
    border-radius: 20px;

    @include mobile {
      margin-bottom: 45px;
    }
  }
}

.portfolio__text_open {
  overflow: visible;
  max-height: 100%;
  padding-bottom: 30px;
}

.swiper-slide-portrolio {
  justify-content: space-between;

  @include mobile {
    flex-direction: column-reverse;
    justify-content: flex-end;
  }
}

.swiper-pagination-current {
  display: inline-block;
  min-width: 48px;
  font-size: 85px;
  line-height: 1;
}

.swiper-pagination-fraction {
  position: absolute;
  display: flex;
  align-items: flex-end;
  width: 103px;
  height: 88px;
  top: auto;
  left: 107px;
  bottom: 2%;
  font-size: 50px;
  color: #333132;
  font-weight: 700;
  letter-spacing: 0.85px;
}

.swiper-portfolio {
  position: relative;
}

.pag-btn {
  display: inline-block;
  position: absolute;
  bottom: 0;
  z-index: 100;
  cursor: pointer;
  bottom: 4%;

  @include mobile {
    display: none;
  }
}

.port-btn-prev {
  & svg path {
    transition: stroke 0.3s ease-in-out;
  }

  &:focus-visible {
    outline: none;
  }

  &:focus-visible svg path,
  &:hover svg path {
    stroke: rgb(4, 73, 19);
  }
}

.port-btn-next {
  display: inline-block;
  position: absolute;
  left: 27%;
  border-radius: 10px;
  transition: outline 0.3s ease-in-out;

  @include mobile {
    display: none;
  }

  & svg path {
    transition: stroke 0.3s ease-in-out;
  }

  &:focus-visible {
    outline: none;
  }

  &:focus-visible svg path,
  &:hover svg path {
    stroke: rgb(4, 73, 19);
  }
}

.port-btn-next.pag-btn.swiper-button-disabled {
  cursor: default;
  & svg path {
    stroke: #b4f0ac;
  }
}

.port-btn-prev.pag-btn.swiper-button-disabled {
  cursor: default;
  & svg path {
    stroke: #b4f0ac;
  }
}

.portfolio-pagination.swiper-pagination-bullets.swiper-pagination-horizontal {
  width: 76%;
}

.portfolio-pagination {
  display: flex;
  justify-content: center;
}

.portfolio-pag-bullet {
  display: inline-block;
  width: 37px;
  height: 4px;
  border-radius: 100px;
  margin-right: 15px;
  background-color: var(--cabalt);
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
  opacity: var(--swiper-pagination-bullet-inactive-opacity, 0.2);
}

.portfolio-pag-bullet-active {
  opacity: 1;
}
