.trust {
  position: relative;
  top: -40px;
  margin-bottom: 58px;
  padding-top: 25px;

  @include mobile {
    padding-bottom: 30px;
    top: 35px;
  }

  &__title {
    margin: 0;
    margin-bottom: 55px;
    text-align: center;
    color: var(--east-bay);
    font-weight: 700;
    line-height: 85%; /* 38.25px */
    text-transform: uppercase;
  }

  &__list {
    justify-content: space-between;
    gap: var(--offset);
    flex-wrap: wrap;

    @include mobile {
      gap: 20px;
    }
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    width: 100%;
    max-width: 370px;
    height: 260px;
    border-radius: 20px;
    background: var(--white-color);
    box-shadow: 0px 0px 100px 0px rgba(0, 0, 0, 0.1);

    @include mobile {
      height: 120px;
    }
  }
}
