html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  background-color: var(--white-color);
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.title {
  font-size: var(--title-size);

  @include tablet {
    font-size: 40px;
  }

  @include small-tablet {
    font-size: 37px;
  }

  @include mobile {
    font-size: 35px;
  }
}

.lock-overflow {
  overflow: hidden;
}

.descr {
  margin: 0;
  font-size: var(--main-size);

  @include tablet {
    font-size: 14px;
  }

  // @include small-tablet {
  //   font-size: 37px;
  // }

  // @include mobile {
  //   font-size: 14px;
  // }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  @include tablet {
    max-width: var(--tablet-container-width);
  }

  @include small-tablet {
    max-width: var(--small-tablet-container-width);
  }

  @include mobile {
    padding-left: 20px;
    max-width: 320px;
    padding-right: 20px;
    // max-width: var(--mobile-container-width);
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.transform-rotate {
  transform: rotateX(180deg);
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

